const Month = ({ month }) => {
  let changedMonth = '';

  if (month === 'Tishri') {
    changedMonth = '';
  }
  if (month === 'Heshvan') {
    changedMonth = '';
  }
  if (month === 'Kislev') {
    changedMonth = '';
  }
  if (month === 'Tevet') {
    changedMonth = '';
  }
  if (month === 'Shevat') {
    changedMonth = '';
  } 
  if (month === 'AdarI') {
    changedMonth = '';
  }
  if (month === 'AdarII') {
    changedMonth = ' ';
  }
  if (month === 'Nisan') {
    changedMonth = '';
  }
  if (month === 'Iyyar') {
    changedMonth = '';
  }
  if (month === 'Sivan') {
    changedMonth = '';
  }
  if (month === 'Tammuz') {
    changedMonth = '';
  }
  if (month === 'Av') {
    changedMonth = '';
  }
  if (month === 'Elul') {
    changedMonth = '';
  }
  
  return changedMonth;
};

export default Month;
